.nav {
    width: 100%;
    position: fixed;
    /* background-color: #F76B1C; */
    /* background-color: #FF9A8B; */
    background-image: linear-gradient(90deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%);

}

.nav-items {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.nav-items li {
    list-style: none;
    color: white;
    font-size: 1rem;
    margin-right: 2rem;
}

.nav-items li:hover {
    color: lightgray;
    transition: 400ms ease-out;
}

.nav-items a {
    text-decoration: none;
}

