.contact {
    padding-top: 5rem;
    padding-bottom: 2rem;
    background-color: #fff;
}

.contact h2 {
    font-size: 3.5rem;
    text-align: center;
    color: #726e6e;
}

.form-items {
    display: flex;
    padding-top: 4rem;
    padding-bottom: 3rem;
}

.form {
    width: 70%;
}

.form h3 {
    margin-bottom: 3rem;
    font-size: 2rem;
    color: #FF6A88;
}

.form form {
    width: 90%;
    display: flex;
    flex-direction: column;
}

input {
    padding: .8rem;
    margin-bottom: 1rem;
    border-radius: .3rem;
    border: .1rem solid #FF9A8B;
    background-color: #FF6A88;
}

input::placeholder {
    color: #333; /* color: #726e6e; */
    font-family: 'Montserrat', sans-serif;
    font-size: .9rem;
}

textarea {
    padding: .8rem;
    margin-bottom: 1rem;
    border-radius: .3rem;
    border: .1rem solid #FF9A8B;
    background-color: #FF6A88;
}

textarea::placeholder {
    color: #333;
    font-family: 'Montserrat', sans-serif;
    font-size: .9rem;
}

form button {
    padding: .8rem;
    max-width: 10rem;
    border: .1rem solid #FF6A88;
    border-radius: 2rem;
    font-size: 1rem;
    color: #FF6A88;
    background-color: #fff;
    transition: 400ms all ease-out;
}

form button:hover {
    background-color: #FF6A88;
    color: #fff;
    border: transparent;
}





.contact-details {
    width: 40%;
    display: flex;
    flex-direction: column;
    color: #333; /*#726e6e*/
}

.contact-details h3 {
    margin-bottom: 3rem;
    font-size: 2rem;
    color: #FF6A88;
}

#telephone {
    margin-top: 2rem;
}

#adresse {
    margin-top: 2rem;
}


.questionnaire {
    padding-top: 4rem ;
    display: flex;
}

.questionnaire h2 {
    font-size: 1.5em;
    text-align: left;
}

.questions {
    margin-bottom: 2rem;
    color: #726e6e;
}

.left {
    width: 30%;
}

.left span {
    color: #726e6e;
}

.left h1 {
    font-size: 2.5rem;
    color: #726e6e;
}

.right {
    width: 70%;
}




@media screen and (max-width: 768px) {
    .contact h2 {
        font-size: 2.5rem;
    }

    .form-items {
        flex-direction: column;
        padding-top: 1rem;
    }

    .form {
        width: 100%;
        margin-bottom: 4rem;
    }
    .form form {
        width: 100%;
    }

    form button {
        padding: .6rem;
        font-size: .9rem;
    }

    .questionnaire {
        flex-direction: column;
    }

    .questionnaire h1 {
        font-size: 2rem;
    }

    .questionnaire h2 {
        font-size: 1.3rem;
    }

    .questions {
        margin-bottom: 1rem;
    }

    .left {
        width: 100%;
        margin-bottom: 2rem;
    }

    .right {
        width: 100%;
    }
}