.homepage {
    width: 100%;
    background-color: #fff;
}


.link-list {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.link-list li {
    margin-right: 1rem;
    color: white;
    font-size: 1rem;
    padding-top: 2rem;
    transition: 400ms ease-out;
}

.link-list li:hover {
    color: #FF6A88;
}

.link-list a {
    text-decoration: none;
}

/* homepage top */

.homepage-top {
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(90deg, #ff9a8ba6 0%, #ff6a888e 55%, #ff99acb0 100%), url('../assets/nadia.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.presentation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
}

.presentation p {
    font-size: 3rem;
}

.presentation h1 {
    font-size: 3.5rem;
    font-weight: bold;
    margin-top: .5rem;
    margin-bottom: 1rem;
}

.presentation h4 {
    font-size: .8rem;
    color: #FF9A8B;
    letter-spacing: .2rem;
}


/* homepage bot */

.homepage-bot {
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.container {
    max-width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.homepage-bot .title {
    padding-bottom: 1rem;
}

.homepage-bot .title span {
    font-size: .8rem;
    color: black;
}

.homepage-bot h2 {
    color: #FF6A88;
    font-size: 3rem;
}

.rows {
    display: flex;
}

.rows .text {
    color: black;
    margin-top: 1rem;
}

.rows .text p {
    line-height: 1.6rem;
}

.rows .text h2 {
    font-size: 1.5rem;
}

.col {
    margin: 1rem;
    text-align: center;
}
.col:hover h2 {
    color: white;
    transition: 400ms all ease-out;
}

.languages, .frameworks, .tools {
    flex: 1;
    padding: 1rem;
    border-radius: .5rem;
    border: .1rem solid #FF9A8B;
    transition: 400ms all ease-out;
}
.languages:hover, .frameworks:hover, .tools:hover {
    transform: scale(1.1);
    background-color: #FF6A88;
    border-radius: .5rem;
}

.icons {
    font-size: 3rem;
    color: black;
}

.icons i {
    margin: .5rem;
}



@media screen and (max-width: 768px) {
    .presentation p {
        font-size: 2rem;
    }

    .presentation h1 {
        font-size: 2.5rem;
    }

    .presentation h4 {
        font-size: .6rem;
    }

    .homepage-bot h2 {
        font-size: 2.5rem;
    }

    .rows {
        flex-direction: column;
    }
}

