.journey {
    padding-top: 5rem;
    padding-bottom: 2rem;
    background-color: #fff;
}

.journey h1 {
    text-align: center;
    font-size: 3.5rem;
    color: #726e6e;
}

.experiences {
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.experiences h2 {
    font-size: 3rem;
    color: #FF6A88;
}

.experiences-content {
    display: flex;
    flex-direction: column;
    margin: 1rem;
}

.contents {
    margin: 1rem;
}

.experiences-content h2 {
    font-size: 2rem;
}

.experiences-content p, h3, h2, strong{
    margin-bottom: 1rem;
}

.content1 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.content2 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.content3 {
    background-color: #FF9A8B;
    padding: 2rem;
    border-radius: 1rem;
}

.experiences-content {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    color: #333;
}

.experiences-content h2 {
    color: #333;
}

.experiences-content .contents p{  
    line-height: 1.8rem; 
}

/* Competences */

.competences {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.competences h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #FF6A88;
}

.competences p{  
    line-height: 1.8rem;
}

.competences p>i {
    margin-right: .5rem;
}

.competences-items {
    display: flex;
    color: #726e6e;
    text-align: center;
}

.items {
    flex: 1;
    padding: 3rem;
    margin: 1rem;
}

.items:hover h3 {
    color: #FF6A88;
    transition: 400ms all ease-out;
}

.item1, .item2, .item3 {
    border: .1rem solid #FF9A8B;
    transition: 400ms all ease-out;
}
.item1:hover, .item2:hover, .item3:hover {
    border: .1rem solid #FF6A88;
}


/* formation  #F76B1C*/

.formation {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.formation h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #FF6A88;
}

.formation-items {
    display: flex;
}

.img-formation {
    display: flex;
    flex: 1;
}

.text-formation {
    flex: 1;
}

.text-formation p {
    color: black;
    line-height: 1.8rem;
}

.text-formation .btn-formation {
    margin-top: 1rem;
}

.text-formation .btn-formation button {
    padding: .8rem;
    font-size: 1rem;
    color: #FF6A88;
    border: .1rem solid #FF6A88;
    border-radius: 2rem;
    background-color: #fff;
    transition: 400ms all ease-out;
}

.text-formation .btn-formation button a {
    text-decoration: none;
    color: #FF6A88;
}
.text-formation .btn-formation button a:hover {
    color: white;
}

.text-formation .btn-formation button:hover {
    background-color: #FF6A88;
    color: #fff;
    border: transparent;
}



@media screen and (max-width: 768px) {
    .journey h1 {
        font-size: 2.5rem;
    }

    .experiences h2 {
        font-size: 2.5rem;
    }

    .experiences-content {
        width: 100%;
    }

    .experiences-content .contents h2 {
        font-size: 1.7rem;
    }

    .competences h2 {
        font-size: 2.5rem;
    }

    .competences-items {
        flex-direction: column;
    }

    .formation h2 {
        font-size: 2.5rem;
        margin-top: 2rem;
    }

    .formation-items {
        flex-direction: column;
    }

    .img-formation {
        justify-content: center;
    }

    .img-formation img {
        width: 20em;
        height: 20rem;
    }
}


@media screen and ( min-width:1024px ) and (max-width:1124px) {
    .formation-items {
        flex-direction: column;
    }

    .img-formation {
        justify-content: center;
    }

    .formation h2 {
        margin-top: 2rem;
    }

    .formation .text-formation {
        text-align: center;
    }
}