.social-networks {
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: #fff;
}

 .title span {
    font-size: .8rem;
}

.social-networks .title h2 {
    font-size: 3rem;
    color: #FF6A88;
}

.social-bloc {
    display: flex;
    color: #333;
}

.social-networks .social-bloc .row {
    margin: 1rem;
}

.container-link {
    flex: 1;
    color: #333;
    font-size: .8rem;
}

.container-link h3 {
    font-size: 1.5rem;
}

.container-link h4 {
    font-size: .9rem;
}


.github {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: .1rem solid #FF6A88;
    padding: 1rem;
    border-radius: .3rem;
    cursor: pointer;
    transition: 400ms ease-out;
}
.github:hover {
    border-color: #FF99AC;
    color: #FF6A88;
}


.linkedin {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: .1rem solid #FF6A88;
    padding: 1rem;
    border-radius: .3rem;
    cursor: pointer;
    transition: 400ms all ease-out;
}
.linkedin:hover {
    border-color: #FF99AC;
    color: #FF6A88;
}

.social-networks .social-bloc a {
    text-decoration: none;
}

.social-bloc .icon-git i, .icon-linked i {
    font-size: 3rem;
}




@media screen and (max-width: 768px) {
    .social-networks .title h2 {
        font-size: 2.5rem;
    }

    .social-bloc {
        flex-direction: column;
    }

    .github {
        text-align: center;
        flex-direction: column-reverse;
    }

    .linkedin {
        text-align: center;
        flex-direction: column-reverse;
    }
}