.footer {
    background-color: #fff;
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.footer .content strong {
    color: #f1254e;
}

.footer-icons i {
    font-size: 3rem;
    margin: 1rem;
    transition: 400ms ease-out;
}
.footer-icons i:hover{
    color: #FF6A88;
}
