.recommendation {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.recommendation .title span {
    font-size: .8rem;
}

.recommendation .title h2 {
    font-size: 3rem;
    color: #FF6A88;
}

/* #c60055 dark
#ff79b0 light
#ff4081 */

.testimonial {
    padding-top: 2rem;
}

.testimonial-items {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

.testimonial-contents {
    display: flex;
    justify-content: center;
}

.testimonial-text {
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-50%, -50%);
    background-color: #333333a2;
    padding: 2rem;
    color: white;
    width: 25rem;
    border-radius: .5rem;
    line-height: 1.5rem;
    text-align: left;
    z-index: 50;
}

.testimonial-text p {
    margin-bottom: 1rem;
}

.testimonial-img img {
    height: 30rem;
    border-radius: .5rem;
    position: relative;
    right: -8rem;
    top: 0;
    z-index: 0;
}







@media screen and (max-width: 768px) {
    .recommendation .title h2 {
        font-size: 2rem;
    }

    .testimonial-img img {
        height: 18rem;
        right: 0;
    }
    
    .testimonial-text {
        opacity: 0;
